<template>
  <div id="share">
    <div class="scrollWrapper" ref="share">
      <div class="textContent" ref="textContent">
        <div class="index">
          <img  v-lazy="src" alt="">
          <div class="content">
            <div class="wrapper">
              <giveVoucher></giveVoucher>
              <div>
                <div class="reciveWrapper">
                  <div class="line1"></div>
                  <span class="text">快来领取吧</span>
                  <div class="line2"></div>
                </div>
                <div class="telNumber" ref="telNumber">
                  <input type="number" v-model="number" placeholder="请输入手机号">
                  <i class="iconfont icon-cuowu" @click="reset"></i>
                </div>
              </div>
            </div>
          </div>
          <a href="https://tklife.mobile.taikang.com/app-t" class="download">下载泰生活APP</a>
        </div>
      </div>
    </div>
    <div class="btn" @click="go">领取优惠券</div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import { Indicator } from 'mint-ui'
export default {
  name: 'HelloWorld',
  data () {
    return {
      number: '',
      src: "./static/image/voucher.png"
    }
  },
  methods: {
    reciveVoucher(){
      if(this.number){
        let reg = /(^1[0-9]{10}$)/;
        if(reg.test(this.number)){
          return true;
        }else {
          this.$toast({
            message: "请输入正确手机号",
            duration: 1000
          })
          return false;
        }
      }else {
        this.$toast({
          message: "请输入手机号",
          duration: 1000
        })
        return false;
      }
    },
    reset(){
      this.number = ''
    },
    go(){
      if(this.reciveVoucher()){
        Indicator.open({
          text: '领取中',
          spinnerType: 'fading-circle'
        });
        setTimeout(() => {
          Indicator.close();
          this.$store.commit("setFromIndex","true");
          this.$router.push({
            name: "recive"
          })
        },2000);
      }
    },
    initScroll(){
      this.$nextTick(() => {
        if(!this.cartScroll){
          this.cartScroll = new BScroll(this.$refs.share,{
            click: true,
            probeType: 3
          });
        }else {
          this.cartScroll.scrollTo(0,0);
        }
      })
    }
  },
  mounted(){
    this.initScroll();
    window.onresize = () => {
      this.cartScroll.refresh();
      let height = this.$refs.telNumber.offsetHeight;
      this.cartScroll.scrollToElement(this.$refs.telNumber, 500, 0, -height);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
#share{
  height: 100%;
  .scrollWrapper{
    overflow: hidden;
    height: 100%;
  }
  .index{
    padding-bottom: 50px;
    box-sizing: border-box;
    img{
      width: 100%;
    }
    .content{
      width: 340px;
      margin: 0 auto;
      padding-top: 20px;
      box-sizing: border-box;
      .wrapper{
        background-color: #fff;
        padding-bottom: 30px;
      }

      .reciveWrapper{
        font-size: 13px;
        line-height: 18px;
        margin-top: 30px;
        .text{
          position: relative;
          &:before{
            content: "";
            position: absolute;
            width: 80px;
            height: 1px;
            background-image: linear-gradient(90deg, rgba(238,238,238,0.50) 0%, #D8D8D8 100%);
            left: -90px;
            top: 50%;
          }
          &:after{
            content: "";
            position: absolute;
            width: 80px;
            height: 1px;
            background-image: linear-gradient(-90deg, rgba(238,238,238,0.50) 0%, #D8D8D8 99%);
            right: -90px;
            top: 50%;
          }
        }
      }
      .telNumber{
        margin-top: 20px;
        position: relative;
        input{
          width: 280px;
          height: 40px;
          outline: none;
          border: 1px solid #999;
          border-radius: 4px;
          padding-left: 10px;
          padding-right: 30px;
          box-sizing: border-box;
        }
        i{
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 40px;
        }
      }
    }
    .download{
      display: inline-block;
      margin: 0 auto;
      font-size: 12px;
      line-height: 17px;
      color: #3998E9;
      margin-top: 30px;
      text-decoration: underline;
    }
  }
}
</style>



