<template>
  <div class="share">
    <div class="scrollWrapper" ref="scrollWrapper">
      <div class="contentWrapper">
        <div class="content">
          <img  v-lazy="src" alt="">
          <div class="textContent">
            <giveVoucher></giveVoucher>
            <div class="ruleWrapper">
              <rule></rule>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="btn" @click="index">去泰生活APP抢券</div>
    <!--<a class="btn" href="https://tklife.mobile.taikang.com/app-t">去泰生活APP抢券</a>-->
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    name: "share",
    data() {
      return {
        src: "./static/image/voucher.png"
      }
    },
    methods: {
      index() {
        this.$router.push({
          name: 'home'
        })
      },
      initScroll(){
        this.$nextTick(() => {
          if(!this.shareScroll){
            this.shareScroll = new BScroll(this.$refs.scrollWrapper,{
              click: true,
              probeType: 3
            });
          }else {
            this.shareScroll.refresh();
          }
        })
      }
    },
    mounted(){
      this.initScroll();
    }
  }
</script>

<style lang="less">
  .share{
    height: 100%;
    .scrollWrapper{
      overflow: hidden;
      height: 100%;
    }
    .contentWrapper{
      padding-bottom: 70px;
      box-sizing: border-box;
    }
    img{
      display: block;
      width: 100%;
    }
    .textContent{
      background-color: #fff;
      width: 340px;
      margin: 0 auto;
      margin-top: 20px;
      padding-bottom: 20px;
      box-sizing: border-box;
    }
  }
</style>



