<template>
  <div class="recive">
    <div class="scrollWrapper" ref="scrollWrapper">
      <div class="contentWrapper">
        <div class="content">
          <div class="logoWrapper"><div class="logo"></div></div>
          <div class="main">
            <div class="title">燕园体检优惠券</div>
            <div class="name">500元体检优惠券</div>
            <div class="time">2018-04-11至2018-08-31</div>
            <div class="line"></div>
            <div class="textWrapper">
              <div class="user">已发放至账户 <span>186010002000</span></div>
              <div class="text">以该手机号登录泰生活APP使用</div>
              <div class="border"></div>
              <rule></rule>
            </div>
          </div>
        </div>
      </div>
      <div class="btn" @click="share">去泰生活APP享用优惠</div>
      <!--<a class="btn" href="https://tklife.mobile.taikang.com/app-t">去泰生活APP享用优惠</a>-->
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    name: "recive",
    methods: {
      share(){
        this.$router.push({
          name: 'share'
        })
      },
      init(){
        if(this.$store.state.fromIndex == "false"){
          this.$router.replace({
            name: 'share'
          })
        }else {
          this.initScroll();
        }
      },
      initScroll(){
        this.$nextTick(() => {
          if(!this.reviceScroll){
            this.reviceScroll = new BScroll(this.$refs.scrollWrapper,{
              click: true,
              probeType: 3
            });
          }else {
            this.reviceScroll.refresh();
          }
        })
      }
    },
    mounted(){
      this.init();
    }
  }
</script>

<style lang="less">
  .recive{
    height: 100%;
    .scrollWrapper{
      overflow: hidden;
      height: 100%;
    }
    .contentWrapper{
      padding-bottom: 70px;
      padding-top: 40px;
      box-sizing: border-box;
    }
    .content{
      width: 340px;
      background-color: #fff;
      margin: 0 auto;
      position: relative;
      padding-bottom: 20px;
      .logoWrapper{
        width: 54px;
        height: 54px;
        border-radius: 50%;
        position: absolute;
        top: -26px;
        left: 50%;
        transform: translateX(-50%);
        background-color: #F0F0F0;
        .logo{
          width: 50px;
          height: 50px;
          border-radius: 50%;
          background: url("../assets/voucher2.png") no-repeat center center;
          background-color: #fff;
          background-size: 26px 26px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
        }
      }
      .title{
        font-size: 16px;
        line-height: 22px;
        color: #333;
        padding-top: 44px;
      }
      .name{
        font-size: 24px;
        line-height: 33px;
        color: #FF5722;
        margin-top: 20px;
      }
      .time{
        font-size: 16px;
        line-height: 22px;
        color: #999;
      }
      .line{
        margin-top: 40px;
        margin-bottom: 15px;
      }
      .textWrapper{
        text-align: left;
        .user{
          font-size: 14px;
          line-height: 22px;
          color: #333;
          padding: 0 20px;
          span{
            color: #FF5722;
          }
        }
        .text{
          font-size: 14px;
          line-height: 20px;
          color: #333;
          margin-top: 4px;
          padding: 0 20px;
        }
        .border{
          width: 319px;
          height: 1px;
          background-color: #f0f0f0;
          margin: 0 auto;
          margin-top: 15px;
          margin-bottom: 20px;
        }
      }
    }
  }
</style>



